import React, { useState } from 'react'
import ArrowKeysReact from 'arrow-keys-react'
import { arrowKeysReactConfig } from '../helper';
import ExtendedNav from './extendedNav'
import SmallNav from './smallNav'

// Arrow key navigation functionality

ArrowKeysReact.config({
    up: () => {
      if(this.props.location.pathname === '/') {
        this.props.history.push('/contact')
        } else if(this.props.location.pathname === '/skills') {
            this.props.history.push('/')
        } else if(this.props.location.pathname === '/projects') {
            this.props.history.push('/skills')
        }  else if(this.props.location.pathname === '/projectone') {
            this.props.history.push('/projects')
        } else if(this.props.location.pathname === '/projecttwo') {
            this.props.history.push('/projectone')
        } else if(this.props.location.pathname === '/projectthree') {
            this.props.history.push('/projecttwo')
        } else if(this.props.location.pathname === '/contact') {
            this.props.history.push('/projectthree')
        }
    },
    down: () => {
      if(this.props.location.pathname === '/') {
        this.props.history.push('/skills')
        } else if(this.props.location.pathname === '/skills') {
            this.props.history.push('/projects')
        } else if(this.props.location.pathname === '/projects') {
            this.props.history.push('/projectone')
        } else if(this.props.location.pathname === '/projectone') {
            this.props.history.push('/projecttwo')
        } else if(this.props.location.pathname === '/projecttwo') {
            this.props.history.push('/projectthree')
        } else if(this.props.location.pathname === '/projectthree') {
            this.props.history.push('/contact')
        } else if(this.props.location.pathname === '/contact') {
            this.props.history.push('/')
        }
    }
  })


const Navbar = (props) => {

    console.log('props from navbar', props)

        // Logic for hovering over icons

        const projectPathnames = () => {
            return (props.location.pathname === '/projects' || props.location.pathname === '/projectone' || props.location.pathname === '/projecttwo' || props.location.pathname === '/projectthree' || props.location.pathname === '/projectfour' )
        }

        return(
            <div id="navbar" alt="navbar" >
                <SmallNav projectPathnames={projectPathnames} pathname={props.location.pathname}  />
            </div>

        )
    }

export default Navbar