import React, { useState } from 'react'
import { Link } from 'react-router-dom'

const SmallNav = (props) => {


    // Same as above, a matching function for highlighting nav buttons, but modified to work with the projects UL which needs slightly different parameters. 

    const highlightProject = (project) => {
        if (props.pathname === `/${project}` || project === props.hoverIcon) {
            return (
                {color: 'white'}
            )
        } else {
            return (
                {color: 'black'}
            )
        }
    }

    console.log(typeof props.pathname)

    const currentHighlight = (linkName) => {
        if (linkName === props.pathname){
            return 'icon_current'
        } else {
            return 'icon'
        }
    }

    const currentHighlightProject = (project) => {
        if (props.pathname === project){
            return 'projectLi_current'
        } else {
            return 'projectLi'
        }
    }

    return (
        <div id="smallNav">
            <ul>
                <li>
                    <Link to="/">
                        <img className={currentHighlight('/')} alt='about' src='/images/about.png' />
                    </Link>
                </li>
                <li>
                    <Link to="/skills">
                        <img className={currentHighlight('/skills')} alt='about' src='/images/skills.png' />
                    </Link>
                </li>
                <li >
                    <Link to="/projects">
                        <img className={currentHighlight('/projects')} alt='about' src='/images/projects.png' />
                    </Link>
                        <ul id={props.projectPathnames() ? 'projectsUlTrue' : 'projectsUl'}>
                            <Link to ="/projectone">
                                <li className={currentHighlightProject('/projectone')}>1</li>
                            </Link>
                            <Link to ="/projecttwo">
                                <li className={currentHighlightProject('/projecttwo')}>2</li>   
                            </Link>
                            <Link to ="/projectthree">
                                <li className={currentHighlightProject('/projectthree')}>3</li>
                            </Link>
                        </ul>
                </li>
                <li>
                    <Link to="/contact">
                        <img className={currentHighlight('/contact')} alt='about' src='/images/contact.png' />
                    </Link>
                </li>
            </ul>
        </div>
    )
}

export default SmallNav