import React, { useState } from 'react'
import { Link } from 'react-router-dom'

const item1 = {
  title: 'Social Club',
  caption: 'A Modern Web Forum',
  picture: './images/usersHighlight.svg',
  link: 'projectone'
}

const item2 = {
  title: 'Wirehead',
  caption: 'A Google Chrome Extension',
  picture: './images/chromeHighlight.svg',
  link: 'projecttwo'
}

const item3 = {
  title: 'Maskot',
  caption: 'An e-Commerce Website',
  picture: './images/dollar-sign.svg',
  link: 'projectthree'
}

const item4 = {
  title: 'From Beyond',
  caption: 'A React/WordPress Blog',
  picture: './images/blog.jpg',
  link: 'projectfour'
}



const gallery = [item1, item2, item3]

const mapGallery = (gallery) => gallery.map(element => 
  <div className="galleryItem">
    <Link to={element.link}>
    <div className="galleryPicture">
      <img src={element.picture} alt="portfolio item" />
    </div>
    <div className="galleryCaption">
      <h3>{element.title}</h3>
      <h4>{element.caption}</h4>
    </div>
    </Link>
  </div>
  
  )

class Projects extends React.Component {
    render() {
    
        return (
          <div id="projects">
              {mapGallery(gallery)}
          </div>
        )
      }
}
 
export default Projects;